<template lang="pug">
p-slide(v-if="resume")
  template(#header)
    h2.my-0 {{resume.title}}
  info-section(v-if="resume.education.list" :name="resume.education.title" icon="fas fa-graduation-cap")
    .d-flex.flex-wrap.flex-column.flex-md-row
      info-card(v-for="(ed,i) in resume.education.list" :key="i" :title="ed.title" 
        :sub-title="ed.period")
        b-card-text
          div {{ed.prof}}
          div {{ed.degree}}
  info-section(v-if="resume.experience.list" :name="resume.experience.title" icon="fas fa-briefcase")
    .d-flex.flex-wrap.flex-column.flex-md-row
      info-card(v-for="(exp, i) in resume.experience.list" :key="i" :title="exp.title" 
        :sub-title="exp.period")
        b-card-text
          div {{exp.position}}
          div {{exp.desc}}
  info-section(v-if="resume.skills.list" :name="resume.skills.title" icon="fas fa-cogs")
    .d-flex.flex-wrap.flex-column.flex-md-row
      info-card(v-for="(sg,i) in resume.skills.list" :key="i" :title="sg.group")
        progress-item(v-for="(el,i) in sg.list" :name="el.name"
          :level="el.level" :key="i")
</template>
<script>
import PSlide from '@/components/Slide.vue';
import InfoSection from '@/components/InfoSection.vue';
import InfoCard from '@/components/InfoCard.vue';
import ProgressItem from '@/components/ProgressItem.vue';
import SlideFetch from '@/components/SlideFetchMixin';

export default {
    data(){
      return{
        resume: null
      }
    },
    async beforeCreate(){
      await SlideFetch.fetchDataJson('resume')
      .then(res => {
        this.resume = res;
      });
    },
    components:{
      PSlide,
      InfoSection,
      InfoCard,
      ProgressItem
    }
}
</script>
