<template lang="pug">
p-slide(v-if="portfolio")
  template(#header)
    h2.my-0 {{portfolio.title}}
  info-section(v-if="portfolio.web.list" :name="portfolio.web.title" icon="fas fa-globe")
    .d-flex.flex-wrap.flex-column
      b-col(cols="12" v-for="w in portfolio.web.list").px-1.px-md-2.mb-3
        proj-card(:projData="w")
  info-section(v-if="portfolio.games.list" :name="portfolio.games.title" icon="fas fa-gamepad")
    .d-flex.flex-wrap.flex-column
      b-col(cols="12" v-for="g in portfolio.games.list").px-1.px-md-2.mb-3
        proj-card(:projData="g")
</template>

<script>
import PSlide from '@/components/Slide.vue';
import InfoSection from '@/components/InfoSection.vue';
import ProjCard from '@/components/ProjCard.vue';
import SlideFetch from '@/components/SlideFetchMixin';

export default {
  data(){
    return{
      portfolio: null
    }
  },
  async beforeCreate(){
    await SlideFetch.fetchDataJson('portfolio')
    .then(res => {
      this.portfolio = res;
    });
  },
  components:{
    PSlide,
    InfoSection,
    ProjCard
  }
}
</script>
